<template>
  <b-col>
    <b-card class="text-left">
      <EditMailButtons />
      <b-btn
        variant="dark"
        class="buttonEdit"
        @click="makeParagraphsClickable"
        style="float: right"
      >
        <div v-if="this.editButtonClicked == true">
          <strong @click="opslaanGegevens">Opslaan</strong>
        </div>
        <div v-else>
          <b-icon-pencil-square></b-icon-pencil-square>
        </div>
      </b-btn>
      <p class="subtitel">Info student(en)</p>
      <div
        v-if="
          stage.studenten !== undefined &&
          stage.studenten !== null &&
          stage.studenten !== ' '
        "
      >
        <b-card-body> </b-card-body>
        <div v-for="student in stage.studenten" :key="student">
          <h6>
            <strong>Naam:</strong>
            <span id="naam" contenteditable="false">{{
              student.voornaam + " " + student.naam + " "
            }}</span>
            <b-button
              @click="
                currentlySelectedStudent = student;
                handledelClickstudent();
              "
              variant="danger"
              >X</b-button
            >
          </h6>
        </div>
      </div>
      <!-- <div v-else> -->
        <p>Geen studenten</p>
      <!-- </div> -->
    </b-card>
  </b-col>
</template>

<script>
import EditMailButtons from "./Mail-Edit-buttons.vue";
import { default as axios } from "axios";

export default {
  components: { EditMailButtons },
  data() {
    return {
      stage: this.$store.state.stages.find(
        (s) => s.id === parseInt(this.$route.query["stageId"])
      ),
      currentlySelectedStudent: "test",
      editButtonClicked: false,
    };
  },
  methods: {
    handledelClickstudent() {
      this.currentlySelectedStudent.stageopdracht = null;
      fetch(
        "http://localhost:7777/student/" + this.currentlySelectedStudent.id,
        {
          method: "PUT",
          body: JSON.stringify(this.currentlySelectedStudent),
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      )
        .then((window.location.href = "http://localhost:8888/"))
        .catch((error) => {
          console.log(error);
        });
    },
    changeClickedState() {
      this.editButtonClicked = false;
    },
    async opslaanGegevens() {
      console.log(this.stage);

      await axios.put(
        "http://localhost:7777/stageopdracht/" + this.stage.id,
        this.stage
      );
    },
    makeParagraphsClickable() {
      var paragraphs = document.getElementsByTagName("span");
      for (let i = 0; i < paragraphs.length; i++) {
        if (paragraphs[i].getAttribute("contenteditable") == "true") {
          paragraphs[i].setAttribute("contenteditable", "false");
        } else {
          paragraphs[i].setAttribute("contenteditable", "true");
        }
      }
      this.changeClickedState();
    },
  },
};
</script>
