<template>
  <div>
    <b-container class="titles-row">
      <b-button @click="postStages()">Load Excel</b-button>
      <b-row class="mb-2" style="margin-left: 2px;">
        <b-col cols="1"></b-col>
        <b-col cols="3">
          <p class="titel">Naam</p>
          <b-input @keyup.enter="getStageExerciseWithName"
            placeholder="Vul de naam hier in" value=""
            class="mt-2"
          ></b-input>
        </b-col>
        <b-col cols="3">
          <p class="titel">Status</p>
          <b-dropdown variant="dark" text="Selecteer Status">
            <div v-for="(status, index) in listOfStatus" :key="index">
              <b-dropdown-item v-on:click="getStagesExerciseWithStatus">{{status}}</b-dropdown-item>
            </div>
            <b-dropdown-divider></b-dropdown-divider>
          </b-dropdown>
        </b-col>
        <b-col cols="3">
          <p class="titel">Titel</p>
          <b-input filter="AI" @keyup.enter="getStageExerciseWithTitle"
            placeholder="Vul titel hier in"
            class="mt-2"
          >
          </b-input>
        </b-col>
      </b-row>
    </b-container>
    <b-container>
      <b-card>
        <b-row>
          <b-col></b-col>
          <b-col></b-col>
          <b-col></b-col>
          <b-col></b-col>
          <b-col>
            <b-button class="mb-1" style="width: 150px" variant="outline-dark"
              >Publiceer
            </b-button>
          </b-col>
        </b-row>

        <b-list-group v-for="stage in $store.state.filterStages" :key="stage.id">
          <b-list-group-item style="padding: 0px" z-index="0" class="bedrijfInfo">
            <b-row style="padding-top: 12px">
              <b-col cols="1">
                <input
                  class="form-check-input secondary"
                  type="checkbox"
                  value=""
                  onclick="selectBedrijf()"
                  z-index="1"
                />
              </b-col>

              <b-col cols="3">
                <p class="listItem">{{ stage.bedrijf.naam }}</p>
              </b-col>
              <b-col cols="3">                
                <b-badge variant="danger" class="badge" v-if="stage.status === 'afgekeurd'">{{stage.status}}</b-badge>
                <b-badge variant="success" class="badge" v-else-if="stage.status === 'goedgekeurd'">{{stage.status}}</b-badge>
                <b-badge variant="warning" class="badge" v-else>{{stage.status}}</b-badge>
                
              </b-col>
              <b-col cols="3">
                <p v-if="stage.naam === undefined" class="listItem">
                  onbekend
                </p>
                <p v-else class="listItem">
                  {{ stage.naam }}
                </p>
              </b-col>
              <b-col class="details-button">
                <b-button
                  class="mb-1"
                  variant="dark"
                  :to="{ path: '/details', query: { stageId: stage.id } }"
                  >Details</b-button
                >
              </b-col>
            </b-row>
          </b-list-group-item>
        </b-list-group>
      </b-card>
    </b-container>
  </div>
</template>

<script>
const axios = require("axios").default;
import { GSheetsHelper } from "../GSheetsHelper.js";
const gSheetsHelper = new GSheetsHelper();



export default {
  name: "Bedrijven-info",
  props: {
    msg: String,
  },
  data() {
    return {
      listOfStatus: ["goedgekeurd", "geen status", "afgekeurd"],
      hasloaded: false
    }

  },
  methods: {
    postStages() {
      console.log(this.hasloaded)
     if (!this.hasloaded) {
        gSheetsHelper.postSheets(
            "13Xl3BejNMzsMvTxpOF_-i71Q6jL29EHH-K5HNzqAIWg",
            "http://localhost:7777/stageopdracht"
        );

        this.hasloaded = true;
      }
      console.log(this.hasloaded)
    },
    async getStages() {
      let stages = await axios.get("http://localhost:7777/stageopdracht");
      return stages.data;
    },
    async getBedrijven() {
      let bedrijven = await axios.get("http://localhost:7777/bedrijf");
      return bedrijven.data;
    },
    getStagesExerciseWithStatus : function (event) {
      this.$store.state.filterStages = this.$store.state.stages.filter(
          stage=> stage.status === event.target.text
      );
      console.log(this.$store.state.filterStages);
      return this.$store.state.filterStages;
    },
    getStageExerciseWithName : function (event){
      this.$store.state.filterStages = this.$store.state.stages
          .filter(stage => this.formatString(stage.bedrijf.naam, event.target.value)
          || this.includesString(stage.bedrijf.naam, event.target.value));
      return this.$store.state.filterStages;
    },
    getStageExerciseWithTitle : function (event){
      if(event.target.value === "onbekend"){
        this.$store.state.filterStages = this.$store.state.stages
            .filter(stage => stage.title === undefined);
        return this.$store.state.filterStages;
      }else {
        this.$store.state.filterStages = this.$store.state.stages
            .filter(stage =>  this.includesString(stage.titel, event.target.value)
            || this.formatString(stage.titel, event.target.value));
        return this.$store.state.filterStages;
      }


    },
    formatString : function (input, output){
        return typeof input === 'string' && typeof output === 'string'
            ? input.localeCompare(output, undefined, { sensitivity: 'accent' }) === 0
            : input === output;
      }
    ,

    includesString(input1, input2){
      return typeof input1 === 'string' && typeof input2 === 'string'
          ? input1.includes(input2)
          : input1 === input2;
    }
  },

  async mounted() {
    this.$store.commit("setStages", { stages: await this.getStages()});
    this.$store.commit("setBedrijven", { bedrijven: await this.getBedrijven() });
  },
  selectBedrijf() {
    console.log("Item geselecteerd.");
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bedrijfInfo:hover {
  background-color: #f9f9f9;
}
.titel {
  font-size: 20px;
  font-weight: bold;
}
.listItem {
  color: black;
}
.badge {
  font-size: 14px;
  font-weight: 500;
  padding: 5px;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  margin: 0 10px;
}
@media screen and (max-width: 950px) {
  .details-button,
  .titles-row {
    display: none;
  }
  .badge {
    width: 100%;
  }
}
</style>
