import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import DetailView from '../views/DetailView.vue'
import LoginView from '../views/LoginView.vue'
import MailLogDetailView from '../views/MailLogDetailView.vue'
import MailView from '../views/Mail.vue'
import SettingsView from '../views/SettingsView.vue'
import MailLogsView from "@/views/MailLogsView"
import ReviewerView from '@/views/ReviewerView'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }, 
  {
    path: '/details',
    name: 'details',
    component: DetailView,
    props: true,
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/MailLogDetails',
    name: 'MailLogDetails',
    component: MailLogDetailView
  },
  {
    path: '/Mail',
    name: 'Mail',
    component: MailView
  },
  {
    path: '/MailLogs',
    name: 'MailLogs',
    component: MailLogsView
  },
  {
    path: '/settings',
    name: 'settings',
    component: SettingsView
  },
  {
    path: '/reviewers',
    name: 'reviewers',
    component: ReviewerView
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
