import {default as axios} from "axios";
import {LocatieHelper} from "@/LocatieHelper";
import {BedrijfHelper} from "@/BedrijfHelper";
import {BedrijfspromotorHelper} from "@/BedrijfspromotorHelper";
import {ContactpersoonHelper} from "@/ContactpersoonHelper";
import {StageHelper} from "@/StageHelper";
import {StudentHelper} from "@/StudentHelper";


export class GSheetsHelper {
    //Sheetid wordt gegeven als je de google sheet in de browser opent.
    async postSheets(sheetId, backendURL) {
        await axios.get(backendURL).then(() => {})

        let url = `https://sheets.googleapis.com/v4/spreadsheets/${sheetId}?key=AIzaSyBvIFtoNA1G0VfYViwfqzloWLEBCkBo_2w&includeGridData=true`
        let stages;
        await axios.get(url).then((response) => {
            stages = response.data.sheets[0].data[0].rowData
        })
        stages.shift();

        let studenturl = `https://sheets.googleapis.com/v4/spreadsheets/1dvzmFNwHEMaFUUFbC7D7S-nKBl_6ULDKsuC6llnZXNU?key=AIzaSyBvIFtoNA1G0VfYViwfqzloWLEBCkBo_2w&includeGridData=true`
        let studenten;
        await axios.get(studenturl).then((response) => {
            studenten = response.data.sheets[0].data[0].rowData
        })
        studenten.shift()

        let studentHelper = new StudentHelper();

        let locatieHelper = new LocatieHelper();
        let bedrijfHelper = new BedrijfHelper();
        let bedrijfpromotorHelper = new BedrijfspromotorHelper();
        let contactpersoonHelper = new ContactpersoonHelper();
        let stageHelper = new StageHelper();


        let stageopdrachten = stages.map(function (s) {
            let locatie = locatieHelper.createLocatie(s)
            let bedrijf = bedrijfHelper.createBedrijf(s, locatie)
            let bedrijfpromotor = bedrijfpromotorHelper.createPersoon(s, 1)
            let contactpersoon = contactpersoonHelper.createPersoon(s)
            return stageHelper.createStage(s, bedrijf, contactpersoon, bedrijfpromotor)
        });

        let studentenmap = studenten.map(function (s){
            return studentHelper.createStudent(s);
        })

        studentHelper.SendsheetbackEnd(studentenmap).then(() => console.log('done'))
        stageHelper.SendsheetbackEnd(stageopdrachten).then(() => console.log('done'))

        setTimeout(window.location.reload(), 1500);

    }



    getStatus(sheetBgColors) {
        if (sheetBgColors.red !== undefined && sheetBgColors.green !== undefined && sheetBgColors.blue !== undefined) {
            return "geen status";
        } else if (sheetBgColors.red === 1) {
            return "afgekeurd";
        } else {
            return "goedgekeurd";
        }
    }
}
