import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sheetUrl: '',
    stages: [],
    filterStages:[],
    authCode: "",
    user: "",
    mail: ""

  },
  getters: {
    getSheetId() {
      return this.sheetUrl.split('/')[5]
    }
  },
  mutations: {
    setStages(state, payload) {
      state.stages = payload.stages
      state.filterStages = state.stages
    },
    setAuthCode(state, payload) {
      state.authCode = payload.authCode
    },
    setUser(state, payload) {
      state.user = payload.user;
    }
  },
  actions: {
  },
  modules: {
  }
})
