<template>
  <b-col>    
    <b-card class="text-left">
      <EditMailButtons />
      <b-btn
        variant="dark"
        class="buttonEdit"
        @click="makeParagraphsClickable"
        style="float: right"
      >
        <div v-if="this.editButtonClicked == true">
          <strong @click="opslaanGegevens">Opslaan</strong>
        </div>
        <div v-else>
          <b-icon-pencil-square></b-icon-pencil-square>
        </div>
      </b-btn>
      <p class="subtitel">Info bedrijfspromotor</p>
      <h6><strong>Titel:</strong> <span id="titel" contenteditable="false">{{ stage.bedrijfspromotor.titel }}</span></h6>
      <h6><strong>Naam:</strong> <span id="naam" contenteditable="false">{{ stage.bedrijfspromotor.naam }}</span></h6>
      <h6><strong>Voornaam:</strong> <span id="voornaam" contenteditable="false">{{ stage.bedrijfspromotor.voornaam }}</span></h6>
      <h6><strong>Telefoonnummer:</strong> <span id="telefoonnummer" contenteditable="false">{{ stage.bedrijfspromotor.telefoonnummer }}</span></h6>
      <h6><strong>Mail:</strong> <span id="mailadres" contenteditable="false">{{ stage.bedrijfspromotor.mailadres }}</span></h6>
    </b-card>
  </b-col>
</template>

<script>
import EditMailButtons from "./Mail-Edit-buttons.vue";
import { default as axios } from "axios";

export default {
  components: { EditMailButtons },
  data() {
    return {
      stage: this.$store.state.stages.find(
        (s) => s.id === parseInt(this.$route.query["stageId"])
      ),
      editButtonClicked: false,
    };
  },
  methods: {
    changeClickedState() {
      this.editButtonClicked = !this.editButtonClicked;
    },
    async opslaanGegevens() {
      console.log(this.stage);
      this.stage.bedrijfspromotor.titel = document.getElementById("titel").textContent;
      this.stage.bedrijfspromotor.naam = document.getElementById("naam").textContent;
      this.stage.bedrijfspromotor.voornaam = document.getElementById("voornaam").textContent;
      this.stage.bedrijfspromotor.telefoonnummer = document.getElementById("telefoonnummer").textContent;
      this.stage.bedrijfspromotor.mailadres = document.getElementById("mailadres").textContent;

      await axios.put(
        "http://localhost:7777/stageopdracht/" + this.stage.id,
        this.stage
      );
    },
    makeParagraphsClickable() {
      var paragraphs = document.getElementsByTagName("span");
      for (let i = 0; i < paragraphs.length; i++) {
        if (paragraphs[i].getAttribute("contenteditable") == "true") {
          paragraphs[i].setAttribute("contenteditable", "false");
        } else {
          paragraphs[i].setAttribute("contenteditable", "true");
        }
      }
      this.changeClickedState();
    },
  }
};
</script>
