<template>
  <div class="card-body">
    <b-card style="height: 650px;position:relative; width:500px; margin:auto">
      <div style="position:absolute; top: 13%; bottom: 0; left: 0; right: 0;">
        <b-row>
          <div class="col-md-12 mb-4">
            <img
              src="../assets/img/1314_logo_pxl_bol.png?h=6ad466dd4a573d9a2ea5f3397ba6486d"
              style="max-width: 200px"
              alt="Logo PXL"
            />
          </div>
        </b-row>

        <b-row style="margin-bottom:100px">
          <h2 style="font-weight: 600">Stagebeheer systeem</h2>
        </b-row>
        <b-row>
          <div class="col-md-12">
            <b-button
              @click="loginWithGoogle"
              style="font-size: 22px; font-weight: 500"
              class="btn btn-dark p-3"
              >
            <b-icon-google style="padding-right:5px; margin-right:5px"></b-icon-google>
              Login met Google</b-button
            >
          </div>
        </b-row>
      </div>
    </b-card>
  </div>
</template>

<script>
/* eslint-disable */



export default {
  name: "LoginView",
  props: {
    msg: String,
  },
  data() {
    return {
      isInit: false,
      isSignIn: false,
    };
  },
  methods: {
    loginWithGoogle () {
      this.$gAuth
        .signIn()
        .then(GoogleUser => {
          var userInfo = {
            google: {
              auth: GoogleUser.getAuthResponse(),
              user: {
                name: GoogleUser.getBasicProfile().getName(),
                email: GoogleUser.getBasicProfile().getEmail(),
                profileImage: GoogleUser.getBasicProfile().getImageUrl()
              }
            }
          }
          this.$store.commit('setUser', {user: userInfo})
          this.$router.push('/')
        })
        .catch(error => {
          console.log('error', error)
        })
    },
    handleClickSignOut() {
      this.$gAuth
        .signOut()
        .then(() => {
          //on success do something
          this.isSignIn = this.$gAuth.isAuthorized;
        })
        .catch((error) => {
          //on fail do something
        });
    },
  },

  created() {
    let that = this;
    let checkGauthLoad = setInterval(function () {
      that.isInit = that.$gAuth.isInit;
      that.isSignIn = that.$gAuth.isAuthorized;
      if (that.isInit) clearInterval(checkGauthLoad);
    }, 1000);
  },
};
</script>

<style scoped>
h3 {
  margin: 40px 0 0;
}
</style>