<template>
    <b-container>
      <b-container>
        <b-row align-h="start">
          <b-col>
            <!-- <h2 class="titel" v-if="stage.titelStageOpdracht !== undefined">
              {{ stage.titelStageOpdracht }}
            </h2>
            <p class="titel" v-else>Geen titel</p> -->
            <p class="titel">{{this.stage.bedrijf.naam}}</p>
          </b-col>
        </b-row>
        <b-row align-h="start">
          <b-col>
            <b-breadcrumb>
              <b-breadcrumb-item
                class="active"
                @click="selectionBreadcrumbTitel(0)"
                >Bedrijf</b-breadcrumb-item
              >
              <b-breadcrumb-item @click="selectionBreadcrumbTitel(1)"
                >Contactpersoon</b-breadcrumb-item
              >
              <b-breadcrumb-item @click="selectionBreadcrumbTitel(2)"
                >Bedrijfspromoter</b-breadcrumb-item
              >
              <b-breadcrumb-item @click="selectionBreadcrumbTitel(3)"
                >Reviewer</b-breadcrumb-item
              >
              <b-breadcrumb-item @click="selectionBreadcrumbTitel(4)"
                >Student</b-breadcrumb-item
              >
            </b-breadcrumb>
          </b-col>
        </b-row>
      </b-container>

      <b-container class="componentField">
        <div v-if="idLastSelected == 0">
          <bedrijf />
        </div>
        <div v-else-if="idLastSelected == 1">
          <contactpersoon />
        </div>
        <div v-else-if="idLastSelected == 2">
          <bedrijfpromoter />
        </div>
        <div v-else-if="idLastSelected == 3">
          <reviewer />
        </div>
        <div v-else-if="idLastSelected == 4">
          <student />
        </div>
        <opties />
      </b-container>
    </b-container>
</template>

<script>
import Bedrijf from "../components/detailspage/Bedrijf-details.vue";
import Contactpersoon from "../components/detailspage/Contactpersoon-details.vue";
import Bedrijfpromoter from "../components/detailspage/Bedrijfpromoter-details.vue";
import Reviewer from "../components/detailspage/Reviewer-details.vue";
import Student from "../components/detailspage/Student-details.vue";
import Opties from "../components/detailspage/Optie-details.vue";

export default {
  components: {
    Bedrijf,
    Contactpersoon,
    Bedrijfpromoter,
    Reviewer,
    Student,
    Opties,
  },
  data() {
    return {
      stage: this.$store.state.stages.find(
        (s) => s.id === parseInt(this.$route.query["stageId"])
      ),
      idLastSelected: 0,

    };
  },
  methods: {
    selectionBreadcrumbTitel(id) {
      this.idLastSelected = id;
      let titels = document.querySelectorAll(".breadcrumb-item");

      titels.forEach((item) => {
        item.classList.remove("active");
      });

      let breadcrumbTitel = titels[id];
      breadcrumbTitel.classList.add("active");
    }
  },
};
</script>

<style scoped>
.componentField {
  margin-bottom: 5%;
}
.active {
  font-weight: bold;
  pointer-events: none;
}
.titel {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 2%;
}
.breadcrumb {
  padding: 10px 0px 10px 15px;
  max-width: 51%;
  background-color: black;
  margin: auto;
  margin-bottom: 2%;
}
a {
  text-decoration: none;
  color: white;
  padding: 0px 5px 0px 5px;
}
a:hover {
  color: black;
  background: #fff;
  transition: all 0.5s ease;
  border-radius: 5px;
}
@media screen and (max-width: 1000px) {
  .breadcrumb-item::before {
    display: none;
  }
  .breadcrumb {
    max-width: 100%;
  }
}
</style>