export class StageHelper {
    createStage(s, bedrijf, contactpersoon, bedrijfspromotor) {
        let aantalStudenten;
        if (s.values[26].formattedValue !== undefined) {
            aantalStudenten = parseInt(s.values[26].formattedValue.split(" ")[0]);
        }
        else {
            aantalStudenten = -1;
        }

        return {
            "naam": s.values[30].formattedValue,
            "status": this.getStatus(s.values[0].effectiveFormat.backgroundColor),
            "bedrijf": bedrijf,
            "contactpersoon": contactpersoon,
            "bedrijfspromotor": bedrijfspromotor,
            "afstudeerrichting": s.values[19].formattedValue,
            "omschrijving": s.values[20].formattedValue,
            "omgeving": s.values[21].formattedValue,
            "randvoorwaarden": s.values[23].formattedValue,
            "onderzoeksthema": s.values[24].formattedValue,
            "inleidendeActiviteiten": s.values[25].formattedValue,
            "aantalStudenten": aantalStudenten,
            "extraInfo": s.values[28].formattedValue,
            "stageperiode": s.values[29].formattedValue
        }
    }

    async SendsheetbackEnd(s) {
        return fetch("http://localhost:7777/stageopdracht/sheet", {
            method: "POST",
            body: JSON.stringify(s),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        }).then((response) => response.json()).then((responseJson) => { return responseJson });
    }

    postStage(url, stage) {
        let newStage;
        fetch(url,
            {
                method: "POST",
                body: JSON.stringify(stage),
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            }).then((response) => {
                newStage = response.json();
            })
            .catch((error) => {
                console.log(error);
            });
        return newStage;
    }

    getStatus(sheetBgColors) {
        if (sheetBgColors.red !== undefined && sheetBgColors.green !== undefined && sheetBgColors.blue !== undefined) {
            return "geen status";
        } else if (sheetBgColors.red === 1) {
            return "afgekeurd";
        } else {
            return "goedgekeurd";
        }
    }
}