import { default as axios } from "axios";

export class ContactpersoonHelper {
    // methode om aan de hand van de sheet (variabele 's') een persoon-object aan te maken
    createPersoon(s) {
        return {
            "titel": s.values[9].formattedValue,
            "voornaam": s.values[11].formattedValue,
            "naam": s.values[10].formattedValue,
            "telefoonnummer": s.values[12].formattedValue,
            "mailadres": s.values[13].formattedValue
        }
    }

    // methode om te kijken of de huidige persoon aanwezig is in de DB
    // indien ja, het ID van de aanwezige persoon wordt geretourneerd
    // indien nee, er wordt een nieuwe persoon toegevoegd in de DB en wordt de nieuwe ID geretourneerd
    async getPersoonId(s) {
        let persoon = this.createPersoon(s);
        let allPersonen;
        await this.getAllPersonen().then((response) => { allPersonen = response; });
        let result = await this.containsPersoon(persoon, allPersonen);
        if (result !== null) {
            return result.id;
        }
        else {
            let newPersoon = this.postPersoon(persoon);
            return newPersoon.id;
        }
    }

    // methode om alle personen op te halen uit de DB
    async getAllPersonen() {
        let allPersonen;
        await axios.get("http://localhost:7777/persoon").then((response) => { allPersonen = response.data; });
        return allPersonen;
    }

    // methode om te controleren of een persoon al aanwezig is in de DB
    containsPersoon(persoon, allPersonen) {
        for (let p of allPersonen) {
            if (this.equalsPersoon(persoon, p)) {
                return p;
            }
        }
        return null;
    }

    // methode om twee personen te vergelijken
    equalsPersoon(p1, p2) {
        if (p1.titel != p2.titel) {
            return false;
        }
        if (p1.voornaam != p2.voornaam) {
            return false;
        }
        if (p1.naam != p2.naam) {
            return false;
        }
        if (p1.telefoonnummer != p2.telefoonnummer) {
            return false;
        }
        return p1.mailadres == p2.mailadres;
    }

    postPersoon(persoon) {
        let newPersoon;
        fetch("http://localhost:7777/persoon", {
            method: "POST",
            body: JSON.stringify(persoon),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        }).then((response) => {
            newPersoon = response.json();
        }).catch((error) => {
            console.log(error);
        })
        return newPersoon;
    }
}