<template>
  <div>
    <b-col>
      <b-card class="text-left" v-if="buttonIsClicked">
        <p class="subtitel">Opties status</p>
        <div v-if="!(goedgekeurd == null)">
          <b-button
            variant="danger"
            @click="
              openFeedback();
              afkeuren();
            "
            >Afkeuren</b-button
          >
          <b-button
            variant="success"
            @click="
              openFeedback();
              goedkeuren();
            "
            >Goedkeuren</b-button
          >
          <b-button variant="primary" @click="bevestigen()">Bevestigen</b-button>
        </div>
        <div v-else>
          <h6>De status is gewijzigd</h6>
        </div>
      </b-card>
      <b-card v-else>
        <div>
          <div class="form-group">
            <p class="subtitel" for="exampleFormControlTextarea1">Feedback</p>
            <textarea
              class="form-control"
              id="exampleFormControlTextarea1"
              rows="3"
            ></textarea>
          </div>
          <div class="optieButtons">
            <b-button variant="danger" @click="openFeedback()"
              >Annuleer</b-button
            >
            <b-button variant="success" @click="wijzigStatus()"
              >Verstuur</b-button
            >
          </div>
        </div>
      </b-card>
      <b-card class="text-left">
        <p class="subtitel">Extra opties</p>
        <b-button @click="handleReviewerToewijzen()" variant="dark">Reviewer toewijzen</b-button>
        <b-button @click="handleStudentToewijzen()" variant="dark">Student toevoegen</b-button>
      </b-card>
    </b-col>
  <b-modal id="modal-1" title="Voeg Reviewer toe" @ok="handleOkClickreview()">
    <b-dropdown :text="currentlySelectedReviewer">
      <b-dropdown-item v-for="reviewer in reviewers" :key="reviewer.naam" @click="currentlySelectedReviewer = reviewer.naam">
        {{reviewer.naam}} 
      </b-dropdown-item>
    </b-dropdown>
  </b-modal>
    <b-modal id="modal-2" title="Voeg Student toe" @ok="handleOkClickstudent(currentlySelectedStudent)">
      <b-dropdown :text="studenttext">
        <b-dropdown-item v-for="student in studenten" :key="student.voornaam" @click="currentlySelectedStudent = student; studenttext= student.voornaam">
          {{student.voornaam}}
        </b-dropdown-item>
      </b-dropdown>
    </b-modal>
  </div>
</template>

<script>
import { jsPDF } from "jspdf";
import { default as axios } from "axios";

export default {
  data() {
    return {
      buttonIsClicked: true,
      goedgekeurd: false,
      stage: this.$store.state.stages.find(
        (s) => s.id === parseInt(this.$route.query["stageId"])
      ),
      reviewers: [],
      studenten: [],
      currentlySelectedReviewer: 'Kies een reviewer',
      currentlySelectedStudent: 'test',
      studenttext: 'Kies een student'
    };
  },
  methods: {
    openFeedback() {
      this.buttonIsClicked = !this.buttonIsClicked;
    },
    afkeuren() {
      this.goedgekeurd = false;
    },
    goedkeuren() {
      this.goedgekeurd = true;
    },
    async wijzigStatus() {
      if (this.goedgekeurd) {
        this.stage.status = "goedgekeurd";
      } else {
        this.stage.status = "afgekeurd";
      }
      await axios.put(
        "http://localhost:7777/stageopdracht/" + this.stage.id,
        this.stage
      );
      this.buttonIsClicked = !this.buttonIsClicked;
      this.goedgekeurd = null;
    },
    getCurrentReviewer() {
      return this.reviewers.filter(reviewer => reviewer.naam === this.currentlySelectedReviewer)[0];
    },
    bevestigen() {
      var doc = new jsPDF();

      doc.setFont("helvetica", "bold");
      doc.setFontSize(32);
      var splitNaam = doc.splitTextToSize(this.stage.naam, 160);
      doc.text(splitNaam, 20, 20);

      doc.setFontSize(12)
      doc.text("Aantal studenten: ", 20, 60);
      doc.text("Afstudeerrichting: ", 20, 70);
      doc.text("Bedrijfsnaam: ", 20, 80);
      doc.text("Aantal medewerkers bedrijf: ", 20, 90);
      doc.text("Aantal IT medewerkers bedrijf: ", 20, 100);
      doc.text("Aantal technische begeleiders: ", 20, 110);
      doc.text("Locatie bedrijf: ", 20, 120);
      doc.text("Bedrijfspromotor: ", 20, 150);
      doc.text("Mailadres bedrijfspromotor: ", 20, 170);
      doc.text("Telefoonnummer bedrijfspromotor: ", 20, 180);
      doc.text("Contactpersoon: ", 20, 190);
      doc.text("Mailadres contactpersoon: ", 20, 210);
      doc.text("Telefoonnummer contactpersoon: ", 20, 220);
      doc.text("Omgeving: ", 20, 230);
      doc.text("Stageperiode: ", 20, 240);
      doc.text("Status: ", 20, 250);

      console.log(this.stage);
      doc.setFont("helvetica", "normal");
      doc.text(`${this.stage.aantalStudenten} student(en)`, 100, 60);
      doc.text(this.stage.afstudeerrichting, 100, 70);
      doc.text(this.stage.bedrijf.naam, 100, 80);
      doc.text(`${this.stage.bedrijf.aantalMedewerkers} medewerker(s)`, 100, 90);
      doc.text(`${this.stage.bedrijf.aantalITMedewerkers} medewerker(s)`, 100, 100);
      doc.text(`${this.stage.bedrijf.aantalTechnischeBegeleiders} begeleider(s)`, 100, 110);
      var locatie1 = `${this.stage.bedrijf.locatie.straat}, ${this.stage.bedrijf.locatie.postcode}`;
      doc.text(locatie1, 100, 120);
      doc.text(this.stage.bedrijf.locatie.gemeente, 100, 130);
      if (this.stage.bedrijf.locatie.locatie != null) {
        doc.text(this.stage.bedrijf.locatie.locatie, 100, 140);
      }
      doc.text(this.stage.bedrijfspromotor.titel, 100, 150);
      doc.text(`${this.stage.bedrijfspromotor.voornaam} ${this.stage.bedrijfspromotor.naam}`, 100, 160);
      doc.text(this.stage.bedrijfspromotor.mailadres, 100, 170);
      doc.text(this.stage.bedrijfspromotor.telefoonnummer, 100, 180);
      doc.text(this.stage.contactpersoon.titel, 100, 190);
      doc.text(`${this.stage.contactpersoon.voornaam} ${this.stage.contactpersoon.naam}`, 100, 200);
      doc.text(this.stage.contactpersoon.mailadres, 100, 210);
      doc.text(this.stage.contactpersoon.telefoonnummer, 100, 220);
      doc.text(this.stage.omgeving, 100, 230);
      doc.text(this.stage.stageperiode, 100, 240);
      doc.text(this.stage.status, 100, 250);

      if (this.stage.omschrijving != null) {
        doc.addPage();
        doc.setFont("helvetica", "bold");
        doc.text("Omschrijving:", 20, 20);
        doc.setFont("helvetica", "normal");
        var splitOmschrijving = doc.splitTextToSize(this.stage.omschrijving, 160);
        doc.text(splitOmschrijving, 20, 30);
      }

      if (this.stage.extraInfo != null) {
        doc.addPage();
        doc.setFont("helvetica", "bold");
        doc.text("Extra info:", 20, 20);
        doc.setFont("helvetica", "normal");
        var splitExtraInfo = doc.splitTextToSize(this.stage.extraInfo, 160);
        doc.text(splitExtraInfo, 20, 30);
      }

      if (this.stage.onderzoeksthema != null) {
        doc.addPage();
        doc.setFont("helvetica", "bold");
        doc.text("Onderzoeksthema:", 20, 20);
        doc.setFont("helvetica", "normal");
        var splitOnderzoeksthema = doc.splitTextToSize(this.stage.onderzoeksthema, 160);
        doc.text(splitOnderzoeksthema, 20, 30);
      }

      doc.save(`${this.stage.naam}.pdf`);
      },
    handleReviewerToewijzen() {
        this.$bvModal.show("modal-1");
        console.log("bloep");
    },
    handleOkClickreview() {
    axios.put(`http://localhost:7777/stageopdracht/${this.stage.id}`,
    {
      "naam": this.stage.naam,
      "status": this.stage.status,
      "bedrijf": this.stage.bedrijf,
      "contactpersoon": this.stage.contactpersoon,
      "bedrijfspromotor": this.stage.bedrijfspromotor,
      "reviewer": {
        "naam": this.getCurrentReviewer().naam,
        "richting": this.getCurrentReviewer().richting
      },
      "afstudeerrichting": this.stage.afstudeerrichting,
      "omschrijving": this.stage.omschrijving,
      "omgeving": this.stage.omgeving,
      "randvoorwaarden": this.stage.randvoorwaarden,
      "onderzoeksthema": this.stage.onderzoeksthema,
      "inleidendeActiviteiten": this.stage.inleidendeActiviteiten,
      "aantalStudenten": this.stage.aantalStudenten,
      "extraInfo": this.stage.extraInfo,
      "stageperiode": this.stage.stageperiode
    } 
    )
    
    console.log("bloebiedeboep")
  },
    handleStudentToewijzen() {
      this.$bvModal.show("modal-2");
    },
    handleOkClickstudent() {
      this.currentlySelectedStudent.stageopdracht = this.stage
      console.log(JSON.stringify(this.currentlySelectedStudent))
      fetch("http://localhost:7777/student/" + this.currentlySelectedStudent.id,
          {
            method: "PUT",
            body: JSON.stringify(this.currentlySelectedStudent),
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            }
          }).then(window.location.href = "http://localhost:8888/").catch((error) => {
            console.log(error);
          });
    }
  },
  async mounted() {
    let data = await axios.get("http://localhost:7777/reviewer");
    this.reviewers = data.data;
    let studentdata = await axios.get("http://localhost:7777/student");
    this.studenten = studentdata.data;
    console.log(this.reviewers);
    console.dir(this.stage);
  },

};
</script>

<style scoped>
.card-body button {
  margin: 5px;
}
.form-group {
  text-align: left;
}
.optieButtons {
  text-align: right;
}
.optieButtons button {
  margin: 5px;
}
</style>