<template>
  <div class="vh-97">
    <b-card class="center-card" align="center">
      <b-card-body>
        <b-button variant="dark" v-b-modal.b-modal-google class="g-signin2 w-1">Verander Google Account</b-button>
        <b-modal id="b-modal-google">

        </b-modal>
        <b-form-file v-model="file" accept=".csv" class="mt-3 w-1_2b" plain></b-form-file>
        <b-button variant="dark" class="w-2_2b" @click="file = null">Reset</b-button>
        <b-button variant="dark" class="w-1">Maak een Back-up</b-button>
        <b-input variant="dark" class="w-1_2 b-inline" v-model="sheetId" placeholder="Plak Sheets URL hier"></b-input>
        <b-button variant="dark" class="w-2_2" @click="postSheetIdToBackEnd()">Sla op</b-button>
        <b-alert v-model="showDismissibleAlert" dismissible :variant="errorvariant">{{errorTxt}}</b-alert>
      </b-card-body>
    </b-card>
  </div>
</template>

<script>
const axios = require('axios').default;
export default {
  data() {
    return {
      sheetId: '',
      file: null,
      googleSignInParams: { clientId: 'YOUR_APP_CLIENT_ID.apps.googleusercontent.com' },
      showDismissibleAlert: false,
      errorTxt:'',
      errorvariant:"danger"
    }
  },
  methods: {
    postSheetIdToBackEnd() {
      this.showDismissibleAlert = false
      //do axios stuff here
      if(this.sheetId.includes("docs.google.com/spreadsheets")){
         axios.post("http://localhost:7777/sheetId", {
         url: this.sheetId.split("/")[5]
          })

        this.errorvariant = "success"
        this.errorTxt = "Sheets URL succesvol geupdated."
        this.showDismissibleAlert=true
      }else {
        this.errorvariant = "danger"
        this.errorTxt = "Foutieve Sheets URL."
        this.showDismissibleAlert=true

      }

    },
    getSheetIdFromBackEnd() {
      //get from backend here
      return axios.get("http://localhost:7777/sheetId")
    }
  },
  async mounted() {
    this.sheetId = await this.getSheetIdFromBackEnd().data;
  }
}
</script>

<style scoped>
.vh-97 {
  height: 97vh !important;
  vertical-align: center;
}
.center-card {
  width: 50em;
  margin: auto;
}
.b-inline {
  display: inline;
  margin-right: 1em;
}
.w-1 {
  width: 100%;
  margin: 2% 0;
}
.w-1_2 {
  width: 75%;
  margin: 2% 1% 2% 0;
}
.w-2_2 {
  width: 24%;
  margin: 2% 0;
}
.w-2_2b {
  width: 24%;
  margin: 0;
}
.form-control-file {
  width: 76%;
  display: inline;
}
</style>