import {default as axios} from "axios";

export class LocatieHelper {
    locaties = [];

//    async loadLocaties() {
//        let allLocaties = await this.getAllLocaties();
//        allLocaties.forEach(l => this.locaties.push(l));
//    }

    // methode om aan de hand van de sheet (variabele 's') een locatie-object aan te maken
    createLocatie(s) {
        return {
            "straat": s.values[2].formattedValue,
            "postcode": s.values[3].formattedValue,
            "gemeente": s.values[4].formattedValue,
            "locatie": s.values[5].formattedValue
        };
    }

    // methode om te kijken of de huidige locatie aanwezig is in de DB
    // indien ja, het ID van de aanwezige locatie wordt geretourneerd
    // indien nee, er wordt een nieuwe locatie toegevoegd in de DB en wordt de nieuwe ID geretourneerd
    async SendsheetbackEnd(s) {
        return fetch("http://localhost:7777/locatie/sheet", {
            method: "POST",
            body: JSON.stringify(s),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        }).then((response) => response.json()).then((responseJson) => { return responseJson });
    }

    async getJSON(locatie) {
        return fetch("http://localhost:7777/locatie", {
            method: "POST",
            body: JSON.stringify(locatie),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        }).then((response) => response.json()).then((responseJson) => { return responseJson });
    }

    // methode om alle locaties op te halen uit de DB
    async getAllLocaties() {
        let allLocaties;
        await axios.get("http://localhost:7777/locatie").then((response) => { allLocaties = response.data; });
        return allLocaties;
    }

}