import {default as axios} from "axios";

export class StudentHelper {

    constructor() {
    }

    // methode om aan de hand van de sheet (variabele 's') een locatie-object aan te maken
    createStudent(s) {

        const Array = s.values[0].formattedValue.split(";");

        return {
            "opleiding": Array[0],
            "afstudeerrichting": Array[1],
            "olod": Array[2],
            "naam":Array[3] ,
            "voornaam":Array[4] ,
            "straat": Array[5],
            "nummer": Array[6],
            "bus": Array[7],
            "postcode": Array[8],
            "gemeente": Array[9],
            "privemail": Array[10],
            "ibamaflex":Array[11] ,
            "gsm": Array[12]
        };
    }

    async SendsheetbackEnd(s) {
        return fetch("http://localhost:7777/student/sheet", {
            method: "POST",
            body: JSON.stringify(s),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        }).then((response) => response.json()).then((responseJson) => { return responseJson });
    }

    // methode om alle Studenten op te halen uit de DB
    async getAllStudents() {
        let allStudents;
        await axios.get("http://localhost:7777/student").then((response) => { allStudents = response.data; });
        return allStudents;
    }

}