import { default as axios } from "axios";

export class BedrijfHelper {
    // methode om aan de hand van de sheet (variabele 's') een bedrijf-object aan te maken
    createBedrijf(s, locatie) {
        return {
            "naam": s.values[1].formattedValue,
            "aantalMedewerkers": s.values[6].formattedValue,
            "aantalITMedewerkers": s.values[7].formattedValue,
            "aantalTechnischeBegeleiders": s.values[8].formattedValue,
            "locatie": locatie
        }
    }

    // methode om alle bedrijven op te halen uit de DB
    async getAllBedrijven() {
        let allBedrijven;
        await axios.get("http://localhost:7777/bedrijf").then((response) => { allBedrijven = response.data; });
        return allBedrijven;
    }

    async postBedrijf(bedrijf) {
        let newBedrijf;
        fetch("http://localhost:7777/bedrijf", {
            method: "POST",
            body: JSON.stringify(bedrijf),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        }).then((response) => {
            newBedrijf = response.json();
        }).catch((error) => {
            console.log(error);
        })
        return newBedrijf;
    }
}