<template>
  <b-col>
    <b-card class="text-left">
      <EditMailButtons />
      <b-btn
        variant="dark"
        class="buttonEdit"
        @click="makeParagraphsClickable"
        style="float: right"
      >
        <div v-if="this.editButtonClicked == true">
          <strong @click="opslaanGegevens">Opslaan</strong>
        </div>
        <div v-else>
          <b-icon-pencil-square></b-icon-pencil-square>
        </div>
      </b-btn>
      
      <span id="naam" class="subtitel">{{ stage.bedrijf.naam }}</span>
      <p></p>
      <h6>
        <strong>Adres:</strong>
        <span id="postcode" contenteditable="false">{{ stage.bedrijf.locatie.postcode }}
        </span>
        <span id="gemeente" contenteditable="false">{{ stage.bedrijf.locatie.gemeente }}</span
        >
        <span id="straat" contenteditable="false">{{ stage.bedrijf.locatie.straat }}</span
        >
      </h6>
      <h6>
        <strong>Aantal medewerkers:</strong>
        <span id="aantalMedewerkers" contenteditable="false">{{
          stage.bedrijf.aantalMedewerkers}}</span>
      </h6>
      <h6>
        <strong>Aantal IT medewerkers:</strong>
        <span id="aantalITMedewerkers" contenteditable="false">{{
          stage.bedrijf.aantalITMedewerkers
        }}</span>
      </h6>
      <h6>
        <strong>Aantal IT Medewerkers ondersteuning:</strong>
        <span id="aantalITMedewerkersOndersteuning" contenteditable="false">{{
          stage.bedrijf.aantalTechnischeBegeleiders
        }}</span>
      </h6>
    </b-card>
    <b-card class="text-left">
      <p class="subtitel">Info opdracht</p>
      <b-row>
        <b-col
          ><h6>
            <strong>Afstudeerrichting:</strong>
            <span id="afstudeerrichting" contenteditable="false">{{
              stage.afstudeerrichting
            }}</span>
          </h6></b-col
        >
      </b-row>
      <b-row>
        <b-col
          ><h6>
            <strong>Omgeving:</strong>
            <span id="omgeving" contenteditable="false">{{
              stage.omgeving
            }}</span>
          </h6></b-col
        >
      </b-row>
      <b-row>
        <b-col
          ><h6>
            <strong>Aantal gewenste stagiars:</strong>
            <span id="aantalStudenten" contenteditable="false">{{
              stage.aantalStudenten
            }}</span>
          </h6></b-col
        >
      </b-row>
      <b-row>
        <b-col>
          <h6
            v-if="
              stage.randvoorwaarden !== undefined &&
              stage.randvoorwaarden !== null
            "
          >
            <strong>Randvoorwaarden:</strong>
              {{ stage.randvoorwaarden }}
          </h6>
          <h6 v-else>
            <strong>Randvoorwaarden:</strong>
            Geen randvoorwaarden
          </h6>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <h6>
            <strong>Onderzoeksthema:</strong>
            <span id="onderzoeksthema" contenteditable="false">{{
              stage.onderzoeksthema
            }}</span>
          </h6>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <h6>
            <strong>Inleidende Activiteit:</strong>
            <span id="inleidendeActiviteiten" contenteditable="false">{{
              stage.inleidendeActiviteiten
            }}</span>
          </h6>
        </b-col>
      </b-row>
    </b-card>
  </b-col>
</template>

<script>
import EditMailButtons from "./Mail-Edit-buttons.vue";
import { default as axios } from "axios";

export default {
  components: { EditMailButtons },
  data() {
    this.setStageForEmail();
    return {
      stage: this.$store.state.stages.find(
        (s) => s.id === parseInt(this.$route.query["stageId"])
      ),
      editButtonClicked: false,
    };
  },
  methods: {
    setStageForEmail() {
      this.$store.state.stageOpdrachtVoorEmail = this.$store.state.stages.find(
        (s) => s.id === parseInt(this.$route.query["stageId"])
      );
    },
    changeClickedState() {
      this.editButtonClicked = !this.editButtonClicked;
    },
    async opslaanGegevens() {
      console.log(this.stage);
      this.stage.bedrijf.naam = document.getElementById("naam").textContent;
      this.stage.bedrijf.aantalMedewerkers =
        document.getElementById("aantalMedewerkers").textContent;
      this.stage.bedrijf.aantalITMedewerkers = document.getElementById(
        "aantalITMedewerkers"
      ).textContent;
      this.stage.bedrijf.aantalTechnischeBegeleiders = document.getElementById(
        "aantalITMedewerkersOndersteuning"
      ).textContent;
      this.stage.bedrijf.locatie.straat =
        document.getElementById("straat").textContent;
      this.stage.bedrijf.locatie.postcode =
        document.getElementById("postcode").textContent;
      this.stage.bedrijf.locatie.gemeente =
        document.getElementById("gemeente").textContent;
      this.stage.afstudeerrichting =
        document.getElementById("afstudeerrichting").textContent;
      this.stage.omgeving =
        document.getElementById("omgeving").textContent;
      
      this.stage.onderzoeksthema =
        document.getElementById("onderzoeksthema").textContent;
      this.stage.inleidendeActiviteiten = document.getElementById(
        "inleidendeActiviteiten"
      ).textContent;
      this.stage.aantalStudenten =
        document.getElementById("aantalStudenten").textContent;

      await axios.put(
        "http://localhost:7777/stageopdracht/" + this.stage.id,
        this.stage
      );
    },
    makeParagraphsClickable() {
      var paragraphs = document.getElementsByTagName("span");
      for (let i = 0; i < paragraphs.length; i++) {
        if (paragraphs[i].getAttribute("contenteditable") == "true") {
          paragraphs[i].setAttribute("contenteditable", "false");
        } else {
          paragraphs[i].setAttribute("contenteditable", "true");
        }
      }
      this.changeClickedState();
    },
  },
};
</script>

<style scoped>
span {
  padding: 2px;
  display: inline;
}
</style>