<template>
  <div className="container">
    <form @submit.prevent="sendEmail">
      <label>Onderwerp</label>
      <input
          type="text"

          v-model="emailData.onderwerp"
          name="emailData.onderwerp"
          placeholder="onderwerp"
      >
      <label>E-mail</label>
      <input
          type="text"
          v-model="emailData.ontvanger"
          name="emailData.ontvanger"
          placeholder="e-mail"
          disabled
      >
      <label>Bericht</label>
      <textarea
          name="emailData.bericht"
          v-model="emailData.bericht"
          cols="30" rows="5"
          placeholder="bericht">
          </textarea>

      <input type="submit" value="Verstuur">
    </form>
  </div>
</template>

<script>

import {SendMail} from "@/SendMail";

// eslint-disable-next-line no-unused-vars
const send = new SendMail();
export default {
  name: 'MailLogDatails',
  data() {
    return {
      emailData:{
        onderwerp: '',
        ontvanger: 'kieran.cremers@student.pxl.be',
        bericht: ''


      }



    }
  },
  methods:{
    sendEmail(){
      try {
        send.postMail(this.emailData);
      } catch(error) {
        console.log({error})
      }
      // Reset form field
      this.emailData.onderwerp = ''
      this.emailData.ontvanger = ''
      this.emailData.bericht = ''
    }
  }
}
</script>

<style scoped>
* {box-sizing: border-box;}

label {
  float: left;
}
input[type=text], [type=email], textarea {
  width: 100%;
  padding: 6px;
  border: 2px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 3px;
  margin-bottom: 8px;
  resize: vertical;
}

input[type=submit] {
  background-color: #4CAF50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #b8c7b9;
}

.container {
  display: block;
  margin:auto;
  text-align: center;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
  width: 30%;
}
</style>