<template>
  <div id="reviewers">
      <b-row>
      <b-col cols="4"></b-col>
      <b-col cols="4">
          <b-card class="reviewer-form">
          <b-alert v-model="error.showDismissibleAlert" dismissible :variant="error.errorVariant">{{error.errorText}}</b-alert>

          <b-form-group>
              <b-form @submit.prevent>
                  <label for="input-voornaam">Naam</label>
                  <b-form-input id="input-voornaam" v-model="reviewer.voornaam" placeholder="Vul reviewer naam in"/>
                  <label for="input-achternaam">Achternaam</label>
                  <b-form-input id="input-achternaam" v-model="reviewer.achternaam" placeholder="Vul reviewer achternaam in"/>
                  <div v-if="reviewer.voornaam !== '' && reviewer.achternaam !== ''">                
                        {{reviewerEmail}}
                  </div>
                  <label for="departement-dropdown">Departement</label>
                  <br/>
                  <b-dropdown :text="dropdownLabel" id="departement-dropdown">
                      <b-dropdown-item v-for="departement in departementen" :key="departement" @click="dropdownLabel = departement">{{departement}}</b-dropdown-item>
                  </b-dropdown>
                  <br/><br/>
                  <b-button :disabled="reviewerNameInvalid" @click="postNewReviewer()">+ Reviewer toevoegen</b-button>
                  <!-- <b-button @click="error.showDismissibleAlert = true">Test the Alert</b-button> -->

              </b-form>
          </b-form-group>
      </b-card>
      </b-col>
      <b-col cols="4"></b-col>
      </b-row>
      
  </div>
</template>

<script>
const axios = require('axios').default;
export default {
    data() {
        return {
            reviewer: {
                voornaam: '',
                achternaam: '',
            },
            departementen: ["DIGITAL", "HEALTHCARE", "BIO", "ELEKTRONICA-ICT", "BUSINESS"],
            dropdownLabel: 'Kies departement',
            error: {
                showDismissibleAlert: false,
                errorText:'',
                errorVariant:"danger"
            }
        }
    },
    methods: {
        // Stuurt de reviewer op naar backend met POST-request
        postNewReviewer() {
            axios.post("http://localhost:7777/reviewer", {
                "naam": `${this.reviewer.voornaam} ${this.reviewer.achternaam}`,
                "richting": this.dropdownLabel
            })
        }
    },
    computed: {
        //Toont dynamisch de email van de lector op de pagina
        reviewerEmail() {
            return `${this.reviewer.voornaam}.${this.reviewer.achternaam}@student.pxl.be`
        },
        //Checken of de namen ingevuld zijn en departement gekozen is
        reviewerNameInvalid() {
            let reviewerNamesNotFilledOut = this.reviewer.voornaam === '' && this.reviewer.achternaam === '';
            console.log(reviewerNamesNotFilledOut && this.dropdownLabel === 'Kies departement')
            if (reviewerNamesNotFilledOut || this.dropdownLabel === 'Kies departement') {
                return true;
            } else {
            return false;
            }
        }
    },
    

}
</script>

<style>
.reviewer-form {
    width: 1fr;
}
</style>